<!--
 * @Description: 侧边栏菜单
 * @Autor: lwq
 * @Date: 2020-07-24 18:28:59
 * @LastEditors: lwq
 * @LastEditTime: 2020-08-27 14:29:13
 * @FilePath: \mrc-base-server-view\src\LAYOUT\sideCompany.vue
--> 
<template>

  <v-list dense
          class="layout-drawer">
    <template v-for="item in list">
      <v-list-item :key="item.menu_name"
                   v-if="item.menusTree.length <= 0"
                   class="listItem"
                   @click="getFunctionUuid(item.function_uuid)"
                   :class='{"listItemBorder": isChild}'
                   :to="item.url">
        <!-- :style="{paddingLeft: depth*2 + 'px'}" -->
        <!-- <v-list-item-icon class="layout-drawer__icon mr-2">
          <v-icon class="material-icons">free_breakfast</v-icon>
        </v-list-item-icon> -->

        <v-list-item-content class="py-4"
                             :style="{marginLeft: depth*15 + 'px'}">
          <v-list-item-title class="menuName">
            <!-- {{INT(item.menu_name)}} -->
            {{INT(item.language_name)}}
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-group :key="item.menu_name"
                    class='listItemBorder pb-0 pt-2'
                    v-else>
        <template v-slot:activator>
          <v-list-item-icon class="layout-drawer__icon mr-2">
            <v-icon class="material-icons">free_breakfast</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="menuName">
              <!-- {{INT(item.menu_name)}} -->
              {{INT(item.language_name)}}
            </v-list-item-title>
          </v-list-item-content>
        </template>

        <side-company :list="item.menusTree"
                      :isChild='false'
                      :depth='depth+1' />
      </v-list-group>
    </template>
  </v-list>
</template>

<script>
import { required } from 'vuelidate/lib/validators'

export default {
  name: 'sideCompany',
  components: {},
  props: {
    list: {
      type: Array
    },
    isChild: {
      type: Boolean,
      default: true
    },
    depth: {
      type: [String, Number],
      default: 0
    }
  },
  data () {
    return {

    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  mounted () {

  },
  beforeRouteUpdate () { },
  methods: {
    // 保存菜单功能的uuid
    getFunctionUuid (uuid) {
      this.$store.commit('setMenuFunctionUuid', uuid)
    }
  },
};
</script>

<style scoped>
.listItemBorder {
  border-bottom: 1px solid #dedede;
  padding-top: 5px;
  padding-bottom: 5px;
}
.layout-drawer .menuName {
  font-size: 15.5px;
}
.v-list-item--link:before {
  background-color: #fff;
}
.v-list-item--active {
  background-color: #fff !important;
  color: #0f46c1;
}
.layout-drawer {
  padding-bottom: 0px;
  padding-top: 0px;
}
.layout-drawer__icon {
  margin-bottom: 8px;
  margin-top: 8px;
}
.layout-drawer
  /deep/
  .v-list-item--active
  .v-list-item__content
  .v-list-item__title {
  font-weight: 600;
  /* font-size: 15px; */
}
.layout-drawer /deep/ .v-list-item__title {
  font-size: 15px;
}
.layout-drawer /deep/ .listItem.v-list-item--active::before {
  top: 50%;
  right: 0;
  width: 4px;
  height: 20px;
  transform: translateY(-50%);
  background-color: #0f46c1;
  opacity: 1;
  transition: none;
}
.layout-drawer /deep/ .theme--light .v-list-item--active:focus::before {
  opacity: 1;
  transition: none;
}
.layout-drawer /deep/ .v-list-item--link:before {
  left: initial;
}
</style>
